.logo {
    $this: &;
    display: flex;
    align-items: center;
    cursor: pointer;
    
    &--logo{
        #{this}__lable{
            display: none;
        }
    }

    &--label{
        #{this}__img{
            display: none;
        }
    }
    
    &__img{
        width: 40px;
        height: 40px;
        & + *{
            margin{ 
                right: 1rem
            }
        }
    }

    &__label {
        font{
            weight: bold
        }
        color: white;
    }
    
}